import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircle } from '@fortawesome/free-solid-svg-icons';

import ImgWrapper from '../../components/UI/ImgWrapper/ImgWrapper';
import ObservationsBg from '../../assets/images/Observations_bg.png';
import RunRiverBg from '../../assets/images/Run_River_bg.jpg';
import LearningBg from '../../assets/images/Learning_bg.jpg';
import classes from './Home.module.css';

class Home extends Component {
	state = {
		backGround: ""
	};
	
	linkMouseEnterHandler = (className) => {
		const viewportWidth = window.innerWidth || document.documentElement.clientWidth;
		if (viewportWidth > 768) {
			this.setState({backGround: className});
		}
	};
	
	linkMouseOutHandler = () => {
		this.setState({backGround: ""});
	};
	
	render () {
		return (
			<div className={[classes.Home, classes[this.state.backGround]].join(" ")}>
				<Link
					onMouseOut={this.linkMouseOutHandler}
					onMouseEnter={() => this.linkMouseEnterHandler("Observations")}
					className={classes.HomeLink}
					to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={ObservationsBg} />
					<h1 className={classes.HomeLinkHeader}><FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />Observations of a Common Past: Adrian Kay Wong</h1>
					<h2 className={classes.HomeLinkSubheader}>Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.</h2>
				</Link>
				<Link onMouseOut={this.linkMouseOutHandler}
				      onMouseEnter={() => this.linkMouseEnterHandler("Run-River")}
				      className={classes.HomeLink}
				      to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={RunRiverBg} />
					<h1 className={classes.HomeLinkHeader}><span className="line-break"><FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />Run River North: </span>Adrian Kay Wong</h1>
					<h2 className={classes.HomeLinkSubheader}>Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.</h2>
				</Link>
				<Link onMouseOut={this.linkMouseOutHandler}
				      onMouseEnter={() => this.linkMouseEnterHandler("Learning")}
				      className={classes.HomeLink}
				      to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={LearningBg} />
					<h1 className={classes.HomeLinkHeader}><span className="line-break"><FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />Learning To Dance: </span>Adrian Kay Wong</h1>
					<h2 className={classes.HomeLinkSubheader}>Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.</h2>
				</Link>
			</div>
		);
	};
}

export default Home;